import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Typography from '@material-ui/core/Typography'
import TextField from '@material-ui/core/TextField'
import Checkbox from '@material-ui/core/Checkbox'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Button from '@material-ui/core/Button'
import { SketchPicker } from 'react-color'
import QRCode from 'algorand-qrcode'
import logo from '../assets/images/logo.png'
import algosdk from 'algosdk'
import MyAlgo from '@randlabs/myalgo-connect'
import svg from '../assets/images/qrlogo.svg'

const styles = (theme) => ({
  root: {
    maxWidth: '60%',
    margin: 'auto',
    padding: theme.spacing(3),
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3)
  },
  title: {
    margin: `${theme.spacing(3)}px 0 ${theme.spacing(2)}px`,
    color: '#333333',
    textAlign: 'center',
    fontSize: '1.2em',
    fontWeight: 'bold'
  },
  textField: {
    width: '100%'
  },
  addressField: {
    minWidth: '90%',
    width: '100%',
    verticalAlign: 'middle'
  },
  formControl: {
    width: '100%'
  },
  colorSelect: {
    width: '40%',
    marginRight: 50
  },
  generateBtn: {
    borderRadius: 3,
    textAlign: 'center',
    margin: 'auto',
    marginLeft: '15px',
    display: 'inline-block',
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)',
    color: '#fff',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: '#fff',
      color: 'rgb(115, 175, 245)'
    }
  },
  walletContainer: {
    width: '100%',
    verticalAlign: 'middle',
    textAlign: 'center'
  },
  qrBtn: {
    borderRadius: 3,
    width: '32px',
    height: '32px',
    padding: '3px',
    textAlign: 'center',
    backgroundRepeat: 'no-repeat',
    margin: 'auto',
    cursor: 'pointer',
    marginLeft: '15px',
    display: 'inline-block',
    backgroundImage: `url(${svg})`,
    borderColor: 'rgb(115, 175, 245)',
    backgroundColor: 'rgb(115, 175, 245)',
    color: '#fff',
    verticalAlign: 'middle',
    '&:hover': {
      backgroundColor: 'rgb(90, 145, 245)',
      color: '#fff'
    }
  },
  myAlgoBtn: {
    borderRadius: 3,
    width: '32px',
    height: '32px',
    padding: '3px',
    textAlign: 'center',

    margin: 'auto',
    cursor: 'pointer',
    marginLeft: '15px',
    display: 'inline-block',

    borderColor: 'rgb(115, 175, 245)',
    backgroundColor: 'rgb(115, 175, 245)',
    color: '#fff',
    verticalAlign: 'middle',
    '&:hover': {
      backgroundColor: 'rgb(90, 145, 245)',
      color: '#fff'
    }
  },
  qrcodeDiv: {
    borderbottom: '10px solid rgb(115, 175, 245)',
    border: '1px solid #333',
    margin: 'auto',
    display: 'block'
  },
  qrImage: {
    borderRadius: '50px',
    width: '256px',
    height: '256px',
    margin: 'auto',
    display: 'block'
  },
  margin: {
    margin: theme.spacing(3)
  },
  textDiv: {
    margin: 'auto',
    display: 'block',
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)'
  },
  subTitle: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  },
  uri: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  }
})
class AssetExample extends Component {
  constructor() {
    super()
    this.state = {
      wallet: 'LP6QRRBRDTDSP4HF7CSPWJV4AG4QWE437OYHGW7K5Y7DETKCSK5H3HCA7Q',
      label: 'emg110@gmail.com',
      note: '',
      xnote: false,
      html: false,
      amount: 1,
      asset: '15993946',
      inverse: false,
      margin: 4,
      uri: 'algorand://',
      errorLevel: 'high',
      version: 'auto',
      dataURL: logo,
      showOptIn: false
    }
    this.generateQRCode = this.generateQRCode.bind(this)


    this.fetchAsset = this.fetchAsset.bind(this)
    this.optInAsset = this.optInAsset.bind(this)
    this.myAlgoConnect = this.myAlgoConnect.bind(this)
  }

  fetchAsset(assetId) {
    const url = `https://api.testnet.algoexplorer.io/v2/assets/${assetId}`
    fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then((response) => response.json())
      .then((data) => {
        this.setState({
          assetName: data.params.name,
          assetURL: data.params.url,
          assetUnit: data.params['unit-name']
        })
      })
      .catch((error) => {
        console.error('Error:', error)
      })
  }

  async optInAsset() {
    const self = this
    try {
      const myAlgoWallet = new MyAlgo()
      const accounts = await myAlgoWallet.connect()
      if (accounts[0]) {
        if (accounts[0].address) {
          self.setState({ wallet: accounts[0].address })
          const algodClient = new algosdk.Algodv2(
            '',
            'https://api.testnet.algoexplorer.io',
            ''
          )
          const params = await algodClient.getTransactionParams().do()
          params.fee = 1000
          params.flatFee = true
          const sender = accounts[0].address
          const recipient = sender

          const amount = 0
          const txn = {
            ...params,
            from: sender,
            type: 'axfer',
            to: recipient,
            assetIndex: Number(self.state.asset),
            amount: amount,
            note: new Uint8Array(Buffer.from(self.state.note))
          }

          const signedTxn = await myAlgoWallet.signTransaction(txn)
          console.log(signedTxn)

          self.setState({ note: JSON.stringify(signedTxn) })
          await algodClient.sendRawTransaction(signedTxn.blob).do()
        }
      }
    } catch (err) {
      console.error(err)
    }
  }


  generateQRCode() {
    const {
      wallet,
      label,
      note,
      xnote,
      html,
      amount,
      asset,
      inverse,
      version,
      margin,
      errorLevel,
      lightColor,
      darkColor
    } = this.state
    const errorCorrectionLevel = errorLevel
    const color = { light: lightColor, dark: darkColor }
   
    this.fetchAsset(asset)

    const opts = {
      wallet,
      note,
      xnote,
      html,
      amount,
      asset,
      inverse,
      version,
      margin,
      errorCorrectionLevel,
      color
    }
    const self = this
    opts.mode = 'Auto'
     QRCode.toDataURL(null, opts)
      .then((res) => {
        self.setState({ dataURL: res[0], uri: res[1], showOptIn: true })
      })
      .catch((err) => {
        console.error(err)
      })
  }

  async myAlgoConnect() {
    const self = this
    try {
      const myAlgoWallet = new MyAlgo()
      const accounts = await myAlgoWallet.connect()
      console.log(accounts[0])
      if (accounts[0]) {
        if (accounts[0].address) {
          self.setState({ wallet: accounts[0].address })
        }
      }
    } catch (err) {
      console.error(err)
    }
  }

  render() {
    const { classes } = this.props
    const versionOptionsArray = []
    versionOptionsArray.push({ value: 'auto', label: 'auto' })
    for (let i = 0; i < 41; i++) {
      versionOptionsArray.push({ value: `${i}`, label: `${i}` })
    }
    return (
      <div>
        <Paper className={classes.root} elevation={4}>
          <Typography type='title' className={classes.title}>
            Algorand QR Code Asset Example
          </Typography>

          <div className={classes.walletContainer}>
            <TextField
              id='wallet'
              label='Wallet address'
              variant='outlined'
              className={classes.addressField}
              value={this.state.wallet}
              onChange={(event) => {
                this.setState({ wallet: event.target.value })
              }}
              margin='normal'
            />
            <Button
              id='wallet-connect-button'
              onClick={this.myAlgoConnect}
              className={classes.myAlgoBtn}
            >
              MyAlgo
            </Button>
          </div>

          <TextField
            id='amount'
            label='Amount'
            value={this.state.amount}
            variant='outlined'
            className={classes.textField}
            type='number'
            onChange={(event) => {
              this.setState({ amount: event.target.value })
            }}
            InputProps={{ inputProps: { min: 0, max: 100 } }}
            margin='normal'
          />
          <TextField
            required
            id='asset'
            label='Asset ID (Numeric)'
            variant='outlined'
            className={classes.textField}
            onChange={(event) => {
              this.setState({ asset: event.target.value })
            }}
            type='number'
            value={this.state.asset}
            margin='normal'
          />
          <TextField
            id='note'
            label='Note'
            variant='outlined'
            className={classes.textField}
            onChange={(event) => {
              this.setState({ note: event.target.value })
            }}
            value={this.state.note}
            margin='normal'
          />
          <FormControlLabel
            control={
              <Checkbox
                // checked={state.checkedA}
                // onChange={handleChange} name="xNote"
                onChange={(event) => {
                  this.setState({ xnote: event.target.value })
                }}
              />
            }
            id='xNote'
            label='XNote'
          />
          <FormControlLabel
            control={
              <Checkbox
                // checked={state.checkedA}
                // onChange={handleChange} name="xNote"
                onChange={(event) => {
                  this.setState({ inverse: event.target.value })
                }}
              />
            }
            id='inverse'
            label='Inverse'
          />
          <FormControlLabel
            control={
              <Checkbox
                onChange={(event) => {
                  this.setState({ html: event.target.value })
                }}
              />
            }
            id='html'
            label='HTML Escaping'
          />
          <FormControl variant='outlined' className={classes.formControl}>
            <InputLabel id='version'>Version</InputLabel>
            <Select
              id='version'
              value={this.state.version}
              onChange={(event) => {
                this.setState({ version: event.target.value })
              }}
              label='Version'
            >
              {versionOptionsArray.map((version) => {
                return (
                  <MenuItem selected value={version.value}>
                    {version.label}
                  </MenuItem>
                )
              })}
            </Select>
          </FormControl>

          <TextField
            id='margin'
            label='Margin'
            variant='outlined'
            className={classes.textField}
            type='number'
            InputProps={{ inputProps: { min: 4 } }}
            value={this.state.margin}
            margin='normal'
            onChange={(event) => {
              this.setState({ margin: event.target.value })
            }}
          />
          <FormControl variant='outlined' className={classes.formControl}>
            <InputLabel id='version'>Error Correction</InputLabel>
            <Select
              id='errorLevel'
              value={this.state.errorLevel}
              // onChange={handleChange}
              label='Error Correction'
              onChange={(event) => {
                this.setState({ errorLevel: event.target.value })
              }}
            >
              <MenuItem value='low'>Low</MenuItem>
              <MenuItem value='medium'>Medium</MenuItem>
              <MenuItem value='quartile'>Quartile</MenuItem>
              <MenuItem selected value='high'>
                High
              </MenuItem>
            </Select>
          </FormControl>

          <div className={classes.margin} />
          <FormControl variant='outlined' className={classes.colorSelect}>
            <SketchPicker
              icker
              name='Light color'
              id='lightColor'
              defaultValue='Light color'
              color={this.state.lightColor}
              onChangeComplete={(color) => {
                this.setState({ lightColor: color.hex })
              }}
            />
          </FormControl>

          <FormControl variant='outlined' className={classes.colorSelect}>
            <SketchPicker
              name='Dark color'
              id='darkColor'
              defaultValue='Dark color'
              color={this.state.darkColor}
              onChangeComplete={(color) => {
                this.setState({ darkColor: color.hex })
              }}
            />
          </FormControl>
          <div className={classes.margin} />
          <Button
            id='generate-button'
            variant='outlined'
            onClick={this.generateQRCode}
            className={classes.generateBtn}
          >
            Create Algorand QR Code
          </Button>
          {this.state.showOptIn && (
            <Button
              id='generate-button'
              variant='outlined'
              onClick={this.optInAsset}
              className={classes.generateBtn}
            >
              Opt-in Asset
            </Button>
          )}

          <div className={classes.margin} />
          <div className={classes.qrcodeDiv}>
            <div className='card'>
              <img className={classes.qrImage} src={this.state.dataURL} />
              <div className='container'>
                <h4>
                  <b>{this.state.assetName}</b>
                </h4>
                <p>{this.state.assetURL}</p>
                <p>{this.state.assetUnit}</p>
              </div>
            </div>
          </div>
        </Paper>
      </div>
    )
  }
}
AssetExample.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired
}
export default withStyles(styles)(AssetExample)
