import React, { Component } from 'react';
import PropTypes from "prop-types";
import {AssetExample, QrCodeGenerator} from 'algorand-qrcode-react';
import { withStyles } from '@material-ui/core/styles'

/* import TxnExample from './TxnExample';
import TxnOfflineExample from './TxnOfflineExample'; */
import logo from './assets/images/logo.png';
import Button from '@material-ui/core/Button';

//import Main from './components/Main';


const styles = (theme) => ({
  
  generateBtn:{
    borderRadius: 3,
    textAligne: 'center',
    margin: '10px',
    display: 'inline-block',
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)',
    color: '#fff',
    '&:hover':{
      backgroundColor: '#fff',
      color: 'rgb(21, 175, 245)',

    }
  },
  generateBtnSelected:{
    borderRadius: 3,
    textAligne: 'center',
    margin: '10px',
    display: 'inline-block',
    backgroundColor: 'rgb(21, 175, 245)',
    borderColor: 'rgb(21, 175, 245)',
    color: '#fff',
    '&:hover':{
      backgroundColor: '#000',
      color: 'rgb(115, 175, 245)',

    }
  }
 
})

class App extends Component {
  constructor() {
    super();
    this.state = {
     showGen: true,
     showAsset: false,
     showTxn: false,
     showTxnOffline: false,


    };
    
}
  render() {
    const { classes }= this.props;
   return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="InstaMonet" />
          <h1>Algorand QR Code React</h1>
          
        </div>

      
        <h3 className="App-desc">Algorand QR Code generator is a JavaScript module that outputs an standard QR code (exportable to SVG, PNG and Base64) from a JSON object. It works in terminal, NodeJS and modern browsers. Complies to Algorand ABNF Grammar and RFC 3986.</h3>
        <Button        
              id="generate-button" 
              variant="outlined" 
              onClick={()=>this.setState({
                showGen: true,
                showAsset: false,
                showTxn: false,
                showTxnOffline: false,
                })} 
                className={this.state.showGen ? classes.generateBtnSelected : classes.generateBtn }
              >
              QR Code Generator
            </Button>
            <Button        
              id="payment-button" 
              variant="outlined" 
              onClick={()=>this.setState({
                showGen: false,
                showAsset: true,
                showTxn: false,
                showTxnOffline: false,
              })} 
              className={this.state.showAsset ? classes.generateBtnSelected : classes.generateBtn }
              >
              Asset QR Code dApp example
            </Button>
            {/* <Button        
              id="asset-button" 
              variant="outlined" 
              onClick={()=>this.setState({
                showGen: false,
                showAsset: false,
                showTxn: true,
                showTxnOffline: false,
              })} 
              className={this.state.showTxn ? classes.generateBtnSelected : classes.generateBtn }
              >
              Payment Transaction QR Code dApp example
            </Button>
            <Button        
              id="trx-button" 
              variant="outlined" 
              onClick={()=>this.setState({
                showGen: false,
                showAsset: false,
                showTxn: false,
                showTxnOffline: true,
              })} 
              className={this.state.showTxnOffline ? classes.generateBtnSelected : classes.generateBtn }
              >
              Offline Authorization QR Code dApp example
            </Button> */}
            
       {this.state.showGen && (<QrCodeGenerator />)}
       {this.state.showAsset && (<AssetExample />)}
     {/*   {this.state.showTxn && (<TxnExample />)}
       {this.state.showTxnOffline && (<TxnOfflineExample />)} */}
      </div>
    );
  }
}

App.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired,

};

export default withStyles(styles)(App);

